import React from "react";
import Circle from "./Circle"

export interface Props {
    circles: {name:String,id:String}[];
    private: Boolean;
}

const CircleList: React.FC<Props> = (props) => {
    console.log(props)
    return(
        <div className="w-full">
            <div className="w-full">
                <div className="w-full items-center font-extrabold">{props.private?"direct messages":"circles"}</div>
                <ul className="w-full">
                    {props.circles?.map((circle: {name: String,id:String})=>{
                        return(
                            <li className="font-semibold text-xs hover:underline"><Circle name={circle.name} id={circle.id}/></li>
                        )
                    })}
                    
                </ul>
            </div>
        </div>
    );
}

export default CircleList;