
import React from "react";

export interface Props {
    message: {author:String,content:String,published:String,me:Boolean};
}
const Message: React.FC<Props> = (props) => {
    if(!props.message.me){
        return(<div className="chat-message">
            <div className="flex items-end">
                <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-2 items-start">
                <div>
                <span className="px-4 py-2 rounded-lg inline-block rounded-bl-none bg-gray-300 text-gray-600">{props.message.content}</span></div>
                </div>
                <img src="https://www.epsi.fr/wp-content/uploads/2019/10/ROMAIN-GONCALVES-1.png" alt="My profile" className="w-7 h-7 rounded-full order-1"></img>
            </div>
        </div>);
    }else{
        return(
            <div className="chat-message">
         <div className="flex items-end justify-end">
            <div className="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-1 items-end">
               <div><span className="px-4 py-2 rounded-lg inline-block rounded-br-none bg-blue-600 text-white ">{props.message.content}</span></div>
            </div>
            <img src="https://i.imgflip.com/4m9grc.jpg" alt="My profile" className="w-7 h-7 rounded-full order-2"/>
         </div>
      </div>
        );
    }
}

export default Message;          